.card-grid {
    display: flex;
    flex-flow: row wrap;
    padding-bottom: 36px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    height: auto;
}

.card{
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    width: 75%;
    opacity: 1;
    background-color: rgba(105, 124, 160, 0.25) !important;
    border-style: none !important;
    border-radius: 0px !important;
}

.card-image {
    width: 100%;
    padding: 5px;
    border-radius: 0px;
    /* min-height: 10vh; */
}

.card-content {
    height: 100% !important;
    width: 100% !important;
    height: 33vh;
}

.card:hover {
    opacity: .85;

}

.card-text {
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
}

.card-title {
    font-family: 'Poiret One', cursive;
    font-weight: bolder;
    font-size: 25px;
}
/* 
.card-col {
    padding-top: 25px;
} */

.card-text, .card-title {
    color: rgb(43, 43, 43);
}

a {
    text-decoration: none !important;
}

.media {
    display: block;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
}

.site-link {
    margin-left: 5px;
    margin-right: 5px;
    color:rgb(49, 84, 119);
    font-size: 30px;
    vertical-align: middle;
}

.site-link:hover {
    color: rgb(43, 43, 43);
}

.card-inner {
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
}

.card-flip:hover .card-inner {
    transform: rotateY(180deg);
    cursor: pointer;
}

.card-front, .card-back {
    position: relative;
    /* display: block;
    position: absolute;
    width: 100%;
    height: 100%; */
    /* position: absolute; */
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.card-back {
    transform: rotateY(180deg);
    display: block;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    /* align-items: center; */
    padding: 16px;
}


@media screen and (max-width: 768px) {
    .card-title {
        font-size: 20px;
     }
    .card-text {
       font-size: 15px;
    }
}
